.cm-s-jsviz {
  &.CodeMirror {
    max-width: 100%;
    height: auto;

    color: #666;

    font-family: "Menlo", "DejaVu Sans Mono", "Consolas", monospace;
    font-size: 0.9rem;
    line-height: calc(1rem + 2px);
  }

  div.CodeMirror-selected {
    background: #d5d5d5;
  }
  .CodeMirror-line::selection,
  .CodeMirror-line > span::selection,
  .CodeMirror-line > span > span::selection {
    background: #d5d5d5;
  }
  .CodeMirror-line::-moz-selection,
  .CodeMirror-line > span::-moz-selection,
  .CodeMirror-line > span > span::-moz-selection {
    background: #d5d5d5;
  }

  .CodeMirror-gutters {
    background: #f8f8f8;
    color: #333;
  }
  .CodeMirror-linenumber {
    color: #aaa;
  }
  .CodeMirror-cursor {
    border-left: 2px solid #222;
  }

  .cm-keyword {
    color: #333;
    font-weight: bold;
  }
  .cm-atom {
    color: #e91e63;
  }
  .cm-number {
    color: #da3024;
  }
  .cm-variable,
  .cm-variable-3,
  .cm-def,
  .cm-type {
    color: #07a;
  }
  .cm-property {
    color: #666;
  }
  .cm-qualifier {
    color: #690;
  }

  .cm-operator {
    color: #999;
  }
  .cm-comment {
    color: #999;
    font-weight: normal;
    font-style: italic;
  }
  .cm-string {
    color: #52a556;
  }
  .cm-string-2 {
    color: #bd6b18;
  } /*?*/
  .cm-meta {
    color: #000;
  } /*?*/
  .cm-builtin {
    color: #333;
  } /*?*/
  .cm-tag {
    color: #007d71;
  }
  .cm-attribute {
    color: #555;
  } /*?*/
  .cm-header {
    color: #ff6400;
  }
  .cm-hr {
    color: #aeaeae;
  }
  .cm-link {
    color: #ad9361;
    font-style: italic;
    text-decoration: none;
  }
  .cm-error {
    border-bottom: 1px solid red;
  }

  &div .CodeMirror-activeline-background {
    background: #efefff;
  }
  &div span.CodeMirror-matchingbracket {
    outline: 1px solid grey;
    color: inherit;
  }
}
